import './header.scss';
import { Link } from 'gatsby';
import PropTypes from 'prop-types';
import React from 'react';
import LogoSvg from 'src/images/logos/logo.svg';
import LogoWhitePng from 'src/images/logos/logo_white.png';
import { GlobalMenus } from 'src/constants';
import { Row, Col } from 'react-bootstrap';

const Header = ({ logoColor }) => {
  const logoImage = logoColor === 'black' ? LogoSvg : LogoWhitePng;
  return (
    <header id="global-header">
      <Row>
        <Col>
          <Link id="main-logo" to="/">
            <img src={logoImage} alt="Lubycon 로고" />
            <span>Lubycon</span>
          </Link>
        </Col>
        <Col>
          <ul className="menus">
            {GlobalMenus.map((menu, index) => {
              return (
                <li key={index}>
                  <Link to={menu.link}>{menu.name}</Link>
                </li>
              );
            })}
          </ul>
        </Col>
      </Row>
    </header>
  )
};

Header.propTypes = {
  logoColor: PropTypes.string,
};

Header.defaultProps = {
  logoColor: 'black',
};

export default Header;
